import { useState } from "react";
import { Link } from "react-router-dom";
import { motion } from "framer-motion";
import styles from "./Contact.module.css";
import Layout from "../../components/Layout/Layout";

const Contact = () => {
    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [message, setMessage] = useState("");
    const [subject, setSubject] = useState("");
    const [submitted, setSubmitted] = useState(false);
    const [error, setError] = useState("");
    
    const handleSubmit = (e: React.FormEvent) => {
        e.preventDefault();
        
        if (!name || !email || !subject || !message) {
            setError(",   ");
            return;
        }
        
        // Here you would normally send the form data to your backend API
        // For now, we'll just simulate a successful submission
        
        setSubmitted(true);
        setError("");
    };
    
    return (
        <Layout>
            <motion.div
                className={styles.contactContainer}
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ duration: 0.5 }}
            >
                <div className={styles.contactContent}>
                    <h1 className={styles.title}>  </h1>
                    
                    {submitted ? (
                        <div className={styles.successMessage}>
                            <h2>   !</h2>
                            <p>      .</p>
                            <Link to="/" className={styles.backLink}>  </Link>
                        </div>
                    ) : (
                        <>
                            <p className={styles.intro}>
                                     ?   ,    
                                     .
                            </p>
                            
                            <form className={styles.contactForm} onSubmit={handleSubmit}>
                                <div className={styles.formGroup}>
                                    <label htmlFor="name" className={styles.label}></label>
                                    <input 
                                        type="text" 
                                        id="name" 
                                        className={styles.input}
                                        value={name}
                                        onChange={(e) => setName(e.target.value)}
                                        placeholder=" "
                                    />
                                </div>
                                
                                <div className={styles.formGroup}>
                                    <label htmlFor="email" className={styles.label}>Email</label>
                                    <input 
                                        type="email" 
                                        id="email" 
                                        className={styles.input}
                                        value={email}
                                        onChange={(e) => setEmail(e.target.value)}
                                        placeholder="your.email@example.com"
                                    />
                                </div>
                                
                                <div className={styles.formGroup}>
                                    <label htmlFor="subject" className={styles.label}></label>
                                    <input 
                                        type="text" 
                                        id="subject" 
                                        className={styles.input}
                                        value={subject}
                                        onChange={(e) => setSubject(e.target.value)}
                                        placeholder="  "
                                    />
                                </div>
                                
                                <div className={styles.formGroup}>
                                    <label htmlFor="message" className={styles.label}></label>
                                    <textarea 
                                        id="message" 
                                        className={styles.textarea}
                                        value={message}
                                        onChange={(e) => setMessage(e.target.value)}
                                        placeholder=" ..."
                                        rows={6}
                                    />
                                </div>
                                
                                {error && <p className={styles.errorText}>{error}</p>}
                                
                                <button type="submit" className={styles.submitButton}> </button>
                            </form>
                            
                            <div className={styles.contactInfo}>
                                <div className={styles.infoItem}>
                                    <h3>Email</h3>
                                    <p>support@furniswap.com</p>
                                </div>
                                
                                <div className={styles.infoItem}>
                                    <h3></h3>
                                    <p>+7 (123) 456-7890</p>
                                </div>
                                
                                <div className={styles.infoItem}>
                                    <h3></h3>
                                    <p>. , . , 86</p>
                                </div>
                            </div>
                            
                            <div className={styles.linkContainer}>
                                <Link to="/" className={styles.backLink}>  </Link>
                            </div>
                        </>
                    )}
                </div>
            </motion.div>
        </Layout>
    );
};

export default Contact; 